import * as React from "react";
import { FunctionComponent, useMemo } from "react";
import CSS, { Property } from "csstype";

type S730Type = {
  s730?: string;

  /** Style props */
  s730AlignContent?: Property.AlignContent;
};

const getStyleValue = (key: string, value: string | number | undefined) => {
  if (value === undefined || value === "") return;
  return { [key]: value };
};
const S730: FunctionComponent<S730Type> = ({ s730, s730AlignContent }) => {
  const s730Style: CSS.Properties = useMemo(() => {
    return {
      ...getStyleValue("alignContent", s730AlignContent),
    };
  }, [s730AlignContent]);

  return (
    <b
      style={{
        flex: "1",
        position: "relative",
        letterSpacing: "0.34px",
        ...s730Style,
      }}
    >
      {s730}
    </b>
  );
};

export default S730;
