import * as React from "react";
import { FunctionComponent, useMemo } from "react";
import CSS, { Property } from "csstype";

type FlightCardType = {
  turkish?: string;
  singaporeAirlines?: string;
  aM?: string;
  h55MNonStop?: string;
  aM1?: string;
  s730?: string;

  /** Style props */
  flightDurationWidth?: Property.Width;
  flightIconWidth?: Property.Width;
  flightIconRight?: Property.Right;
  flightIconLeft?: Property.Left;
};

const getStyleValue = (key: string, value: string | number | undefined) => {
  if (value === undefined || value === "") return;
  return { [key]: value };
};
const FlightCard: FunctionComponent<FlightCardType> = ({
  turkish,
  singaporeAirlines,
  aM,
  flightDurationWidth,
  flightIconWidth,
  flightIconRight,
  flightIconLeft,
  h55MNonStop,
  aM1,
  s730,
}) => {
  const flightDurationStyle: CSS.Properties = useMemo(() => {
    return {
      ...getStyleValue("width", flightDurationWidth),
    };
  }, [flightDurationWidth]);

  const flightIconStyle: CSS.Properties = useMemo(() => {
    return {
      ...getStyleValue("width", flightIconWidth),
      ...getStyleValue("right", flightIconRight),
      ...getStyleValue("left", flightIconLeft),
    };
  }, [flightIconWidth, flightIconRight, flightIconLeft]);

  return (
    <div
      style={{
        alignSelf: "stretch",
        borderRadius: "6px",
        backgroundColor: "#fff",
        border: "0.7px solid #cecece",
        display: "flex",
        flexDirection: "row",
        padding: "0px 6px 0px 25px",
        alignItems: "center",
        justifyContent: "center",
        gap: "50px",
        textAlign: "left",
        fontSize: "16px",
        color: "#57575d",
        fontFamily: "Roboto",
      }}
    >
      <div
        style={{
          width: "20%",
          display: "flex",
          flexDirection: "row",
          alignItems: "center",
          justifyContent: "flex-start",
          gap: "7px",
        }}
      >
        <img
          style={{ position: "relative", width: "34.77px", height: "46px" }}
          alt=""
          src={turkish}
        />
        <div style={{ position: "relative", letterSpacing: "0.26px" }}>
          {singaporeAirlines}
        </div>
      </div>
      <div
        style={{
          flex: "1",
          display: "flex",
          flexDirection: "row",
          alignItems: "center",
          justifyContent: "space-between",
          fontSize: "21px",
          color: "#1262af",
        }}
      >
        <div style={{ position: "relative", width: "93px", height: "52px" }}>
          <div
            style={{
              position: "absolute",
              width: "100%",
              top: "0%",
              left: "0%",
              letterSpacing: "0.32px",
              fontWeight: "500",
              display: "inline-block",
            }}
          >
            {aM}
          </div>
          <div
            style={{
              position: "absolute",
              width: "79.51%",
              top: "63.46%",
              left: "0%",
              fontSize: "16px",
              letterSpacing: "0.26px",
              color: "#646468",
              display: "inline-block",
            }}
          >
            SIN
          </div>
        </div>
        <div
          style={{
            position: "relative",
            width: "121px",
            height: "55px",
            textAlign: "center",
            fontSize: "16px",
            color: "#8e8e94",
            ...flightDurationStyle,
          }}
        >
          <img
            alt=""
            src="/MyImages/flighticon.svg"
            style={{
              position: "absolute",
              height: "27.46%",
              width: "88.52%",
              top: "0%",
              right: "5.5%",
              bottom: "72.54%",
              left: "5.98%",
              maxWidth: "100%",
              overflow: "hidden",
              maxHeight: "100%",
              ...flightIconStyle,
            }}
          />
          <div
            style={{
              position: "absolute",
              top: "65.45%",
              left: "0%",
              letterSpacing: "0.21px",
            }}
          >
            {h55MNonStop}
          </div>
        </div>
        <div
          style={{
            position: "relative",
            width: "91px",
            height: "52px",
            textAlign: "right",
          }}
        >
          <div
            style={{
              position: "absolute",
              width: "100%",
              top: "0%",
              left: "0%",
              letterSpacing: "0.32px",
              fontWeight: "500",
              display: "inline-block",
            }}
          >
            {aM1}
          </div>
          <div
            style={{
              position: "absolute",
              width: "81.25%",
              top: "63.46%",
              left: "17.65%",
              fontSize: "16px",
              letterSpacing: "0.26px",
              color: "#646468",
              display: "inline-block",
            }}
          >
            LAX
          </div>
        </div>
      </div>
      <div
        style={{
          width: "128px",
          display: "flex",
          flexDirection: "row",
          alignItems: "center",
          justifyContent: "center",
          textAlign: "center",
          fontSize: "24px",
          color: "#fba403",
        }}
      >
        <img
          style={{ position: "relative", width: "1px", height: "100px" }}
          alt=""
          src="/vector-1.svg"
        />
        <b style={{ flex: "1", position: "relative", letterSpacing: "0.34px" }}>
          {s730}
        </b>
      </div>
    </div>
  );
};

export default FlightCard;
